export default {
	data() {
		return {
			consultingFeedbackContent: ''
		};
	},
	onLoad:function(option){
		
	},
	methods: {
		bindTextAreaBlur:function(e){
			this.consultingFeedbackContent = e.detail.value
		},
		submit () {
			if (!this.consultingFeedbackContent) {
				uni.showToast({
					icon: 'none',
					title: '反馈内容不能为空！',
					duration: 2000
				})
				return false;
			}
			 // 加载动画
			 uni.showLoading({
				 title: '提交中'
			 });
			this.$http.post(
				'System/sub_information',
				'\r\n--XXX' +
				'\r\nContent-Disposition: form-data; name="question"' +
				'\r\n' +
				'\r\n' + this.consultingFeedbackContent +
				'\r\n--XXX--'
				).then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					 uni.showToast({
						icon: 'success',
						title: '提交成功',
						duration: 2000,
						success: function() {
							uni.switchTab({
								url: "../../pages/dealer/index"
							})
						}
					 })
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		}
	}
}
